import React from "react"
import { graphql, useStaticQuery} from 'gatsby'
import styled from 'styled-components'
import BlogItem from './blog_item'
import { color, padding, margin, borderRadius, textSize , boxShadow} from '../../util/style_property'

const BlogList = ({ edges, prependHeadingText = ""}) => {

  const Wrapper = styled.section`
    display: block;
    width: 100%;
    padding: ${padding.m};
    background-color: ${color.background_light_gray};
    border-radius: ${borderRadius.m};
    box-shadow: ${boxShadow.s};
  `

  const Heading = styled.h2`
    color: ${color.text_black};
    font-size: ${textSize.h2};
    box-sizing: border-box;
    width: 100%;
    padding: ${padding.s} ${padding.s} ${padding.s} ${padding.m};
    border-left: ${padding.s} solid ${color.green};
    background-color: ${color.white};
  `

  return (
    <Wrapper>
      <Heading>{prependHeadingText + "記事一覧"}</Heading>
      <BlogItems edges = {edges} />
    </Wrapper>
  )
}

const BlogItems = ({edges}) => {
  const items = edges.map((edge) => {
    let node = edge.node
    let src = node.thumbnail ? node.thumbnail.fluid.src : ""
    return (
      <BlogItem
        title={node.title}
        date={node.updatedAt}
        src={src}
        link={`./blog/${node.slug}`}
        tags = {node.tags}
      />)
  })

  return items
}

export default BlogList